/**
 * Br til element me nafni og btir vi rum elementum ea texta num.
 * @param {string} name Nafn  elementi
 * @param {Record<string, string | EventListener>} attributes Eiginleikar  elementi, strengir ea fll.
 * @param  {...string | HTMLElement} children Hugsanleg brn: nnur element ea strengir
 * @returns {HTMLElement} Elementi me gefnum brnum
 */
export function el(name, attributes = {}, ...children) {
  const e = document.createElement(name);

  for (const key of Object.keys(attributes)) {
    if (typeof attributes[key] === "function") {
      e.addEventListener(key, attributes[key]);
      continue;
    }
    e.setAttribute(key, attributes[key]);
  }

  for (const child of children) {
    if (child === null || child === undefined) {
      console.warn("Child is null", name, attributes);

      continue;
    }

    if (typeof child === "string" || typeof child === "number") {
      e.appendChild(document.createTextNode(child.toString()));
    } else {
      e.appendChild(child);
    }
  }

  return e;
}

/**
 * Fjarlgir ll brn `element`.
 * @param {Element} element Element sem  a tma
 */
export function empty(element) {
  if (!element || !element.firstChild) {
    return;
  }

  while (element.firstChild) {
    element.removeChild(element.firstChild);
  }
}