
// skilgreina path a spurninga database 
const flokkar = ["Flokkanmer", "Undirflokkur", "Erfileikastig", "Gastig", "Spurning", "Svar"];
/**
 * Fall sem tekur inn array rows og mappar t hvert stak sem 
 * object tilheyrandi kvenum flokki sem er skilgreint  "flokkar"
 * @param {*} row -
 * @returns {}
 */
export function mapRowToObject(row) {
    const object = {};
    flokkar.forEach((key, index) => {
        object[key] = row[index] ?? "";
    });
    return object;
}