import Papa from "papaparse";
/**
 * Fall sem tekur inn path  gagnagrunn sem er  textaformi og 
 * skilar t gagnagrunninum  array formi
 * @param {string} database - strengur sem inniheldur URL path  gagnagrunn skr
 * @returns {Promise}- gagnagrunnurinn  array formi
 */

export async function fetchQ(database) {
    try {
        const data = await fetch(database);  // skja ggn fr URL 
        if (!data.ok || data === null) {   // passa a einhver ggn su til
            throw new Error(`Villa a skja ggn: ${data.status}`);
        }
        // breyta gagnagrunni  array form
        const data_text = await data.text();
        const data_parsed = Papa.parse(data_text, {
            header: false,
            skipEmptyLines: true,
        });   

        return data_parsed.data; 

    } catch (error) {
        console.warn('Error fetching data:', error);
        return null;
    }
}


