// skilgreina path a static spurninga database 
//const flokkar = ["Flokkanmer", "Undirflokkur", "Erfileikastig", "Gastig", "Spurning", "Svar"]; 6 lengd

export function byrjaleik(questions){

    const fjoldi=questions.length;
    var count=0;                  //talning,  hvaa spurningu vi erum
    var nuverandiSpurning=questions[0];   // heldur um nverandi spurningu
    var synaSpurningu = true; //bool til ess a vihalda hvort vi sum a sna spurn ea svar

    //nnari upplsingar
    var flokkur = nuverandiSpurning[0];
    var undirflokkur = nuverandiSpurning[1];
    var erfidleiki = nuverandiSpurning[2];

    const flokkar = {
        1: "Almenn kunntta",
        2: "Nttra og vsindi",
        3: "Bkmenntir og listir",
        4: "Saga",
        5: "Landafri",
        6: "Skemmtun og afreying",
        7: "rttir og tmstundir"
    };

    const erfidleikastig = {
        1: "Ltt", 
        2: "Meal", 
        3: "Erfi"
    }

    document.getElementById("spurninga-flokkur").textContent=flokkar[flokkur];
    document.getElementById("spurninga-undirflokkur").textContent=undirflokkur;
    document.getElementById("spurninga-erfidleiki").textContent=("Erfileikastig: "+erfidleikastig[erfidleiki]);

    //fall til a uppfra nanari uppl. egar skipt er um spurningar
    function uppfaeraDeets(){
        flokkur = nuverandiSpurning[0];
        undirflokkur = nuverandiSpurning[1];
        erfidleiki = nuverandiSpurning[2];
        document.getElementById("spurninga-flokkur").textContent=flokkar[flokkur];
        document.getElementById("spurninga-undirflokkur").textContent=undirflokkur;
        document.getElementById("spurninga-erfidleiki").textContent=("Erfileikastig: "+erfidleikastig[erfidleiki]);

    }

    //initializear forriti  fyrstu spurningu
    var nuverandiSpurningText=nuverandiSpurning[4]; 
    document.getElementById("spurninga-box").textContent=nuverandiSpurningText;

    function tilbaka(){
        if(count!==0){
            count-=1;
            nuverandiSpurning = questions[count];
            var spurning = nuverandiSpurning[4];
            document.getElementById("spurninga-box").textContent=spurning;
            synaSpurningu=true;
            uppfaeraDeets();
        }
        else{
            alert(" ert  fyrstu spurningunni!")
        }
    }


    function next(){
        console.log("afram");
        if(count!==(fjoldi-1)){
            count+=1;
            nuverandiSpurning = questions[count];
            var spurning = nuverandiSpurning[4];
            document.getElementById("spurninga-box").textContent=spurning;
            synaSpurningu=true;
            uppfaeraDeets();
        }
        else{
            alert(" ert  seinustu spurningunni!")
        }
    }
    
    //skilgreinir sna svar snir svar ef spurningin er uppi, snir spurningu ef svari er uppi
    function synaSvar(){
        if(synaSpurningu){
            var svar = nuverandiSpurning[5];
            document.getElementById("spurninga-box").textContent=svar;
            synaSpurningu = false;
        }
        else{
            var spurning = nuverandiSpurning[4];
            document.getElementById("spurninga-box").textContent=spurning;
            synaSpurningu = true;
        }
    }

    const tilBakaButton = document.querySelector("#button-til-baka")
    const synaSvarButton = document.querySelector("#button-svar");
    const nextButton = document.querySelector("#button-next");


    tilBakaButton.addEventListener("click", async (event) => {
        event.preventDefault();
        tilbaka();
    });
    
    nextButton.addEventListener("click", async (event) => {
        event.preventDefault();
        next();
    });

    synaSvarButton.addEventListener("click", async (event) => {
        event.preventDefault();
        synaSvar();
    });

    //hgri og vinstri lyklar fra spurningarnar fram og til baka, Spacebar snir svari.
    document.addEventListener("keydown", (event) => {
        switch (event.key) {
            case "ArrowLeft":
                event.preventDefault(); 
                tilbaka();
                break;

            case "ArrowRight":
                event.preventDefault(); 
                next();
                break;

            case " ":
                event.preventDefault(); 
                synaSvar();
                break;
        }
    });

    //hr
    const leikjaskjar = document.querySelector("#game-screen");
    document.addEventListener("keydown", (event) => {
        if(event.key === "f"){
            if(!document.fullscreenElement){
                leikjaskjar.requestFullscreen();
            }
            else{
                document.exitFullscreen?.();
            }
        }
    });
}