
/**
 * 
 * @param {*} questions 
 * @returns 
 */
export function convertObjecttoArray(questions) {
    const convertQ = questions.map(q => [
        q.Flokkanmer, 
        q.Undirflokkur,
        q.Erfileikastig,
        q.Gastig,
        q.Spurning,
        q.Svar
    ]);
    console.log(convertQ);
    return convertQ;
}